.app-header {
    -webkit-align-items: baseline;
            align-items: baseline;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.search-panel {
    width: auto;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin-right: 25px;
}
.todo-list-item {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.todo-list-item__label{
    width: 100%;
    cursor: pointer;
}

.todo-list-item__label.done{
    text-decoration: line-through;
}

.todo-list-item__label.important{
    font-weight: bold;
    color: tomato;
}
.item-add-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.app {
    margin: 0 auto;
    padding: 50px;
    max-width: 700px;
}

.top-panel{
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
